import { Component } from '@angular/core';
import { UserDataService } from '../services/user-data.service';
import { AngularFirestore } from '@angular/fire/firestore';
import { filter, distinct, map, mergeAll, distinctUntilChanged, flatMap } from 'rxjs/operators';
import { from, Observable } from 'rxjs';
import { AngularFireStorage } from '@angular/fire/storage';

@Component({
  selector: 'app-tab2',
  templateUrl: 'tab2.page.html',
  styleUrls: ['tab2.page.scss']
})
export class Tab2Page {

  constructor(
    private userData: UserDataService,
    private firestore: AngularFirestore,
    private afStorage: AngularFireStorage,

  ) { }

  items = []

  ngOnInit() {

    let token = ''
    this.userData.subject
      .pipe(
        filter(e => !!e),
        filter(e => !!e['favorites']),
        filter(e => !!e['email']),
        filter(e => e['email'] != 'guest'),
        distinct(e => {
          token = localStorage.getItem('token')
          return e.favorites
        }),
        map(e => {
          return this.firestore.collection('usersData')
            .doc(e['email'])
            .collection('favorites')
            .valueChanges()
        }),
        mergeAll(),
        distinctUntilChanged((p, c) => {
          this.items = []
          return p.length === c.length
        }),
        map(e => from(e)),
        mergeAll(),
        map(e => this.searchById(e['id'])),
        mergeAll(),
        map(e => e.payload.doc.data()),
      )
      .subscribe(
        e => {
          e['imgURL'] = this.url(e['img'], token)
          this.items.push(e)
        })




    this.userData.subject
      .pipe(
        filter(e => !!e),
        filter(e => e['email'] == 'guest'),
        filter(e => e['tab'] == 'favorites'),
        map(e => {
          let favorites = JSON.parse(localStorage.getItem('favorites')) || {}
          this.items = []
          return Object.entries(favorites)
        }),
        map(e => from(e)),
        mergeAll(),
        map(e => from(e)),
        mergeAll(),
        filter(e => e[0]),
        map(e => this.searchById(+e)),
        mergeAll(),
        map(e => e.payload.doc.data()),
        map(e => {
           this.downloadImage(e['img']).toPromise().then(img => e['imgURL']=img)
           return e
        }),
      )
      .subscribe(
        e => this.items.push(e)
      )

  }

  private downloadImage(id): Observable<any> {
    return this.afStorage.ref(`/images/${id}`).getDownloadURL()
  }
  private url(img, token): string {
    return `https://firebasestorage.googleapis.com/v0/b/shopping-backend-firebas-8d9fe.appspot.com/o/images%2F${img}?alt=media&token=${token}`
  }

  searchById(id): Observable<any> {
    return this.firestore.collection('products', ref =>
      ref.where('id', '==', id)
        .limit(1))
      .snapshotChanges()
      .pipe(flatMap(clients => clients))

  }

}
